//初始化vue项目app模板
import Vue from 'vue'
import App from './App'

//vue路由配置引入文件
import Router from 'vue-router'
Vue.use(Router)

//vue-resource引入(发送http请求)
import VueResource from 'vue-resource'
Vue.use(VueResource)
Vue.http.options.emulateJSON = true

//elementUI引入
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-default/index.css'
Vue.use(ElementUI)

//路由页面引入
import Index from './pages/Index'
import Login from './pages/Login'
import Registration from './pages/Registration'
new Vue({
	el: '#app',
	router: new Router({
		mode: "history",
		routes: [{
			path: "/",
			component: Index
		},{
			path: "/Login",
			component: Login
		},
		{
			path: "/Registration",
			component: Registration
		}]
	}),
	template: '<App/>',
	components: { App }
})
//全局ajax函数
//header == 1 默认请求头
Vue.prototype.ajax = function(url,data,header,callback){
	if(header == 1){
		header = {headers: {'Content-Type': 'x-www-from-urlencoded'}};
	}
	this.$http.post(url,data,header).then((data)=>{
		console.log(data)
	})
}
//全局时间计算器
Vue.prototype.setTime = function(data){

}



