<template lang="pug">
.login
	el-card.box-card
		.logo LOGO
		el-form(v-bind:model="formData")
			el-form-item(label="手机号")
				el-input(v-model="formData.telphone")
			el-form-item(label="密码")
				el-input(v-model="formData.password" type="password")
				a.forget 忘记密码
			el-form-item
				el-button.loginBtn(type="success" @click="postMessage" size="large") 登 录
				router-link.signIn(to="/Registration") 注册
</template>

<script>
export default {
	data(){
		return {
			formData: {
				telphone: '',
				password: ''
			}
		}
	},
	methods : {
		postMessage : function(){
			this.ajax("http://120.26.100.13:8088/login",this.formData,1);
		},
		postBack : function(data){
			console.log(data)
		}
	}
}
</script>

<style scoped lang="stylus">
$width = 350px
.login
	width $width
	position absolute
	top 50%
	left 50%
	transform translate(-50%,-50%)
.box-card
	width $width
	height (@width+100)
	padding (@width/17.5) (@width/14)
.logo
	height 120px
	font-weight bold
	font-size 50px
	font-family "Microsoft YaHei",sans-serif
	text-align center
	color #324057
.loginBtn
	width 150px
	font-size 18px
	font-family "Microsoft YaHei",sans-serif
	font-weight bold
	display block
	margin 20px auto 0
.forget
.signIn
	font 12px normal
	color #3182bd
	cursor pointer
	&:hover
		text-decoration underline
.forget
	position absolute
	top 10px
	right 0
.signIn
	position absolute
	right 5px
	bottom 0
</style>



